const HeaderNavSize = document.querySelector('.header-container');
const linkMenu = document.querySelector('.active');
const linkNumber = HeaderNavSize.children.length -1;

// Active link sizing ".active"

let widthLinkMenu = HeaderNavSize.clientWidth / linkNumber;
let percentage = widthLinkMenu / 10;

widthLinkMenu = widthLinkMenu + percentage;
linkMenu.style.width = widthLinkMenu + "px";

console.log(linkMenu.clientWidth);

// Active link resizing ".active"

window.addEventListener("resize", () => {
  widthLinkMenu = HeaderNavSize.clientWidth / linkNumber;
  percentage = widthLinkMenu / 10;
  widthLinkMenu = widthLinkMenu + percentage; 

  console.log(widthLinkMenu);
  
  linkMenu.style.width = widthLinkMenu + "px";
  linkMenu.style.minWidth = widthLinkMenu + "px";
});

// Mobile menu open/close

const iconMobile = document.querySelector('.header-menu-icon');
const headerMenu = document.querySelector('.header-container');

let isMenuOpen = false;
let mobileMenuDOM;

const closeMenu = () => {
  mobileMenuDOM.classList.remove('open');
};

const createMobileMenu = () => {
  mobileMenuDOM = document.createElement('div');
  mobileMenuDOM.classList.add('mobile-menu');
  mobileMenuDOM.addEventListener('click', event => {
    event.stopPropagation();
  });
  console.log(headerMenu.querySelector('div'));

  const allLink = document.querySelectorAll('.nav');
  console.log(allLink)
  allLink.forEach (a => {
    console.log(a)
    mobileMenuDOM.append(a.cloneNode(true));
    headerMenu.append(mobileMenuDOM);
  })
  
};

const openMenu = () => {
  if(mobileMenuDOM) {

  } else {
    createMobileMenu();
  }
  mobileMenuDOM.classList.add('open');
};

const toggleMobileMenu = event => {
  if (isMenuOpen) {
    closeMenu();
} else {
  openMenu();
 }
 isMenuOpen = !isMenuOpen;
};

iconMobile.addEventListener("click", (event) => {
  event.stopPropagation();
  toggleMobileMenu();
});

window.addEventListener('click', () => {
  if(isMenuOpen) {
    toggleMobileMenu();
  }
});

window.addEventListener('resize', event => {
  if(window.innerWidth > 480 && isMenuOpen) {
    toggleMobileMenu();
  }
});
